// 导入请求对象
import request from '@/untils/request'

// 封装一个获取用户自己频道的接口
export const ownChannel = () => {
  return request({
    url: '/v1_0/user/channels'
  })
}

// 封装一个获取所有频道的接口
export const allChannel = () => {
  return request({
    url: '/v1_0/channels'
  })
}
// 封装一个更新我的频道的接口
export const updateChannels = (data) => {
  return request({
    url: '/v1_0/user/channels',
    method: 'put',
    data
  })
}
