<template>
  <div>
    <van-nav-bar>
      <!-- 自定义左侧 -->
      <template #left>
        <div class="logo"></div>
      </template>
      <template #right>
        <van-button round icon="search" type="primary" to="/search">搜索</van-button>
      </template>
    </van-nav-bar>
          <!--nav-->
    <van-tabs v-model="active">
      <!-- title每个标签页的标题，也就是频道的名字 -->
      <!-- 在van-tab里就放了这个频道下的内容 -->
      <van-tab v-for="item in ownChannels" :key="item.id" :title="item.name">
        <articleList :channel_id="item.id"/>
      </van-tab>
      <div class="icon-wrap">
        <van-icon name="wap-nav" @click="showChannel=true"/>
      </div>
    </van-tabs>
    <router-view ></router-view>
    <van-action-sheet v-model="showChannel" title="频道管理">
<!--:value="active" @input="active=$event" 可替换成 v-model=“active”-->
      <channel-edit :ownChannels="ownChannels" :allChannels="allChannels" :value="active" @input="active=$event" @fixIndex="active--"/>
    </van-action-sheet>
  </div>
</template>
<script>
import { ownChannelsAPI, allChannelsAPI } from '@/api'
import articleList from '@/views/home/articleList'
import channelEdit from '@/views/home/channelEdit'

export default {
  name: 'myHome',
  components: {
    articleList, channelEdit
  },
  data () {
    return {
      ownChannels: [],
      allChannels: [],
      showChannel: false,
      active: 0
    }
  },
  async created () {
    // 读取本地存储
    const local = JSON.parse(window.localStorage.getItem('HMTT81_CHANNELS'))

    if (this.$store.state.tokenObj.token || !local) {
      const res1 = await ownChannelsAPI()
      this.ownChannels = res1.data.data.channels
    } else {
      // 代表有数据，就取出来给ownChannels
      this.ownChannels = local
    }

    const res2 = await allChannelsAPI()
    this.allChannels = res2.data.data.channels
  },
  methods: {
    // add (item) {
    //   this.ownChannels.push(item)
    // }
  }
}
</script>
<style scoped lang="less">
.van-nav-bar {
  background-color: #ff6232;
  .logo {
    width: 100px;
    height: 38px;
    background: url('~@/assets/logo.png') no-repeat center/contain;
  }
  .van-button--primary {
    height: 30px;
    background-color: rgba(255, 255, 255, 0.2);
    border: none;
    .van-button__content {
      color: white;
    }
    .van-icon {
      color: white;
    }
  }
}
.van-tabs {
::v-deep .van-tabs__wrap {
  width: 340px;
}

.icon-wrap {

  position: absolute;
  right: 0;
  top: 0;
  width: 35px;
  height:35px;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;

.van-icon {
  font-size:30px;
}
}
}
</style>
