<template>
  <div>
    <van-nav-bar title="搜索中心" left-arrow @click-left="$router.go(-1)" />

    <form action="/">
      <van-search v-model="textValue" @input="recording" shape="round" show-action placeholder="请输入搜索关键词" @search="onSearch" >
        <template #action>
          <div @click="onSearch">搜索</div>
        </template>
      </van-search>
    </form>
    <van-cell value="历史记录" v-show="!resultShow"/>
    <div class="history" v-show="!resultShow">
      <van-cell :value="item" v-for="(item,idx) in historyList" :key="idx" @click="toGo(item)"/>
    </div>
    <div class="connection" v-show="resultShow">
      <van-cell  icon="search"  v-for="(item,idx) in colorList" :key="idx" >
        <div v-html="item">{{item}}</div>
      </van-cell>
    </div>
  </div>
</template>
<script>
import { searchHistoryAPI } from '@/api'

export default {
  name: 'searchIndex',
  data () {
    return {
      textValue: '',
      historyList: JSON.parse(window.localStorage.getItem('history')) || [],
      resultList: [],
      resultShow: false
    }
  },
  methods: {
    onSearch () {
      if (this.textValue) {
        this.$router.push('/search/result/' + this.textValue)
        this.$store.commit('saveVal', this.textValue)
        if (!this.historyList.includes(this.textValue)) {
          this.historyList.unshift(this.textValue)
          window.localStorage.setItem('history', JSON.stringify(this.historyList))
        }
      }
    },
    async recording () {
      if (this.textValue !== '') {
        this.resultShow = true
        const res = await searchHistoryAPI({
          q: this.textValue
        })
        this.resultList = res.data.data.options
      } else {
        this.resultShow = false
      }
    },
    toGo (item) {
      this.textValue = item
      this.onSearch()
    }
  },
  computed: {
    colorList () {
      // 要根据服务器返回的数组重新得到一个新数组
      // 新数组长度要跟服务器返回的数组长度一样，既然如此，用map方法
      return this.resultList.map((v) => {
        // 对v进行处理：把搜索的关键字包成一个span
        // 我希望忽略大小写以及能替换所有，就得用正则表达式
        const reg = new RegExp(this.textValue, 'ig')

        return v.replace(reg, `<span style="color:red">${this.textValue}</span>`)
      })
    }
  }
}
</script>
<style scoped lang="less">
.van-cell--borderless{
  border: 0;
}
</style>
