<template>
  <div>
    <van-nav-bar title="登录" />
    <van-form @submit="onSubmit">
      <van-field
        v-model="info.mobile"
        label="手机号"
        placeholder="手机号"
        :rules="rules.mobile"
      />
      <van-field
        v-model="info.code"
        label="验证码"
        placeholder="验证码"
        :rules="rules.code"
      />
      <div style="margin: 16px;">
        <van-button round block type="info" native-type="submit" :loading="isLoading" loading-text="登录中...">登录</van-button>
      </div>
    </van-form>
  </div>
</template>
<script>
import { loginAPI } from '@/api'

export default {
  name: 'myLogin',
  data () {
    return {
      info: {
        mobile: '13800138000',
        code: '246810'
      },
      isLoading: false,
      rules: {
        mobile: [
          // 代表必填
          { required: true, message: '请填写手机号' },
          // 我要验证输入内容是不是符合手机号的格式
          { pattern: /^1[2-9]\d{9}$/, message: '请填写正确的手机号' }
        ],
        code: [
          { required: true, message: '请填写验证码' },
          { pattern: /^\d{6}$/, message: '请输入6位数字验证码' }
        ]
      }
    }
  },
  methods: {
    // 发送登录请求
    async onSubmit () {
      // 一点击就改变按钮状态
      this.isLoading = true
      try {
        const res = await loginAPI(this.info)
        // console.log(res)
        this.$store.commit('changeUserInfo', '')
        this.$store.commit('changeTokenObj', res.data.data)
        this.$toast.success('登录成功！')
        // 跳转到指定页面
        if (this.$route.query.back) {
          await this.$router.push(this.$route.query.back)
        } else {
          await this.$router.push('/layout/home')
        }
      } catch {
        this.$toast.fail('登录失败！')
      }
      this.isLoading = false
    }
  }
}
</script>
<style scoped>
.van-button{
  background:linear-gradient(to right top,#ff6464,#fd6633);
  border: 0;
}
</style>
