import request from '@/untils/request'

// 后续还可以继续封装其他跟用户模块有关的接口函数
// 封装获取个人信息的请求函数
import store from '@/store'

export const login = (data) => {
  return request({
    url: '/v1_0/authorizations',
    method: 'post',
    data
  })
  // 因为这个接口可能多个地方要用
  // 而多个地方用的时候，根据他们不同的需求.then里的代码是不一样
  // 所以不能写死.then
  // 解决办法：就直接把发请求得到的promise对象，给他返回出去
  // 那样调用者想干嘛就可以自己写.then
  // .then ( res => {

  //     console.log(res)
  // })
  // .catch(err=>{
  // })
}
export const userInfo = () => {
  return request({
    url: '/v1_0/user/profile',
    headers: {
      Authorization: 'Bearer ' + store.state.tokenObj.token
    }
  })
}

// 封装修改用户名字接口
export const editInfo = (data) => {
  return request({
    url: '/v1_0/user/profile',
    method: 'patch',
    data
  })
}

// 封装一个修改用户头像的接口函数
export const editPhoto = (data) => {
  return request({
    url: '/v1_0/user/photo',
    method: 'patch',
    data
  })
}
