import Vue from 'vue'
import VueRouter from 'vue-router'

import login from '@/views/login'
import layout from '@/layout'
import home from '@/views/home'
import ask from '@/views/ask'
import video from '@/views/video'
import mine from '@/views/mine'
import edit from '@/views/mine/edit'
import articleDetail from '@/views/home/articleDetail'
// 导入store
import store from '@/store'
import { Toast } from 'vant'
import search from '@/views/search'
import searchResult from '@/views/search/searchResult'
import chat from '@/views/mine/chat'
Vue.use(VueRouter)

const routes = [
  { path: '/', redirect: '/layout/home' },
  { name: 'login', path: '/login', component: login },
  { name: 'search', path: '/search', component: search },
  { name: 'result', path: '/search/result/:key', component: searchResult, meta: { needKeep: true } },
  { name: 'edit', path: '/mine/edit', component: edit, meta: { needLogin: true } },
  { name: 'chat', path: '/mine/chat', component: chat },
  {
    name: 'layout',
    path: '/layout',
    component: layout,
    children: [
      // 子路由一般不加/，代表拼接父路由来访问
      { name: 'home', path: 'home', component: home, meta: { needKeep: true } },
      { name: 'ask', path: 'ask', component: ask },
      { name: 'video', path: 'video', component: video },
      { name: 'mine', path: 'mine', component: mine, meta: { needLogin: true } }
    ]
  },
  { name: 'articleDetail', path: '/articleDetail/:id', component: articleDetail }
]

const router = new VueRouter({
  routes
})

// 官方提供的解决办法：
// 解决放行到别的页面会报错的提醒
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push (location, onResolve, onReject) {
  if (onResolve || onReject) return originalPush.call(this, location, onResolve, onReject)
  return originalPush.call(this, location).catch(err => err)
}

// 全局前置守卫
// 全局：所有路由都会触发
// 前置：进入路由前触发
// to： 去哪的路由信息
// from： 从哪来的路由信息
// next： 是一个函数，调用就代表放行，不调用就不放行
router.beforeEach((to, from, next) => {
  // 去的要登录的页面
  if (to.meta.needLogin) {
    // 有token代表登录了
    if (store.state.tokenObj.token) {
      // 代表登录了
      next()
    } else {
      // 放行到/login的路由，也就是登录页
      next({
        name: 'login',
        query: {
          back: to.path
        }
      })
      Toast.fail('请先登录')
    }
  } else {
    // 去的是不需要登录的页面
    next()
  }
})

export default router
