/*
● 因为vuex的数据是存在内存中的，所以页面一刷新数据就没了
●  所以我们要把token再长久保存（可以用localStorage）
●  那也就意味着，我保存token时既要保存到vuex，也要保存到localStorage
●  一般情况下，如果要保存token到本地存储，还会封装一个专门用来操作本地存储token的文件
●  来到 utils文件夹，新建 auth.js文件，在这个文件里专门封装操作token的本地存储的方法
 */
// auth: 权限的意思
// token就是跟权限有关
// 在这个文件里封装三个方法：保存token、获取token、删除token

const TOKEN_KEY = 'hmtt81_token'

// 保存本地存储里的token
export const setToken = (obj) => {
  // 本地存储只能存字符串数据
  // 所以对象、数组这些不能直接存
  // 所以要把他们先转成JSON字符串，再存
  localStorage.setItem(TOKEN_KEY, JSON.stringify(obj))
}

// 获取本地存储里的token
export const getToken = () => {
  return JSON.parse(localStorage.getItem(TOKEN_KEY))
}

// 删除本地存储里的token
export const removeToken = () => {
  localStorage.removeItem(TOKEN_KEY)
}
