<template>
  <div class="channel-edit">
    <!-- 我的频道 -->
    <div>
      <div class="box-title">
        <span>我的频道</span>
        <van-button style="float: right" size="mini" type="danger" plain @click="isShow=!isShow">{{ isShow?'取消':'编辑' }}</van-button>
      </div>
      <van-grid>
        <van-grid-item v-for="(item,index) in ownChannels" :key="item.id"  >
          <span :class="{active:index===value}" @click="changeActive(index)">{{item.name}}</span>
          <van-icon name="cross" v-show="isShow &&item.name!=='推荐'" @click="del(index)"/>
        </van-grid-item>
      </van-grid>
    </div>

    <!-- 可选频道 -->
    <div style="margin-top:20px">
      <div>
        <span>可选频道</span>
      </div>
      <van-grid>
        <van-grid-item  v-for="item in optChannels" :key="item.id" :text="item.name" @click="addChannels(item)"/>
      </van-grid>
    </div>
  </div>
</template>

<script>
import { updateChannelsAPI } from '@/api'

export default {
  props: {
    ownChannels: {
      // 限制传递的数据类型
      type: Array,
      // 限制必须要传递
      required: true
    },
    allChannels: {
      // 限制传递的数据类型
      type: Array,
      // 限制必须要传递
      required: true
    },
    value: {
      type: Number,
      required: true
    }
  },
  data () {
    return {
      newChannel: this.ownChannels,
      isShow: false
    }
  },
  computed: {
    optChannels () {
      return this.allChannels.filter(v => !this.ownChannels.find(val => val.id === v.id))
    }
  },
  methods: {
    addChannels (item) {
      this.newChannel.push(item)
      // console.log(item)
      this.save(item)
      // console.log(res)
    },
    async save (item) {
      if (this.$store.state.tokenObj.token) {
        const channels = this.ownChannels.map((v, i) => {
          return {
            id: v.id,
            seq: i
          }
        })
        // 发请求即可
        await updateChannelsAPI({
          channels
        })
      } else {
        window.localStorage.setItem('HMTT81_CHANNELS', JSON.stringify(this.newChannel))
      }
    },
    del (idx) {
      if (idx < this.value) {
        this.$emit('fixIndex')
        this.newChannel.splice(idx, 1)
        this.save()
      } else {
        this.newChannel.splice(idx, 1)
        this.save()
      }
    },
    changeActive (index) {
      // 就该子传父 ($emit)
      this.$emit('input', index)

      // 再通知父组件一个事件
      this.$emit('close')
      this.save()
    }
  }
}

</script>

<style lang="less" scoped>
.box-title{
height: 24px;
}
.channel-edit {
  padding: 0 20px;
}
.van-grid-item{
  span{
    font-size: 14px;
  }
  .van-icon{
    font-size: 14px;
    position: absolute;
    right: 3px;
    top: 3px;
  }
}
.active{
  color: red;
}
</style>
