<template>
  <div>
    <div class="content" style="padding: 16px 16px 160px;">
      <van-cell >
        <template>
          <div class="commentArea">
            <div class="left">
              <van-image round fit="cover" width="100%" height="100%" :src="currentItem.aut_photo"/>
            </div>
            <div class="middle">
              <div class="author_name">{{ currentItem.aut_name }}</div>
              <div class="comment_val">
               {{currentItem.content}}
              </div>
              <div class="timer">
                <span>{{ currentItem.pubdate|relvTime }}</span>
              </div>
            </div>
          </div>
        </template>
      </van-cell>
      <van-divider >回复列表</van-divider>
      <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
        <van-cell v-for="(item,idx) in list" :key="idx">
          <template>
        <div class="commentArea">
          <div class="left">
            <van-image round fit="cover" width="100%" height="100%" :src="item.aut_photo"/>
          </div>
          <div class="middle">
            <div class="author_name">{{ item.aut_name }}</div>
            <div class="comment_val">
              {{ item.content }}
            </div>
            <div class="timer">
              <span>{{ item.pubdate|relvTime }}</span>
            </div>
          </div>
        </div>
          </template>
        </van-cell>
      </van-list>
      <div class="commit">
        <van-cell >
          <van-field
            center
            clearable
            placeholder="请输入评论内容"
            v-model="commentVal"
          >
            <template #button>
              <van-button size="small" type="primary" @click="send">发布</van-button>
            </template>
          </van-field>
        </van-cell>
      </div>
    </div>
  </div>
</template>
<script>
import { getCmtAPI, sendCmtAPI } from '@/api'
export default {
  name: 'replyComment',
  props: {
    currentItem: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      list: [],
      loading: false,
      finished: false,
      offset: null,
      commentVal: ''
    }
  },
  methods: {
    async onLoad () {
      this.loading = false
      const res = await getCmtAPI({
        type: 'c',
        source: this.currentItem.com_id,
        offset: this.offset
      })
      // console.log(res)
      this.list.push(...res.data.data.results)
      // 把本次的最后一条id作为下次请求的参数
      this.offset = res.data.data.last_id
      // 数据全部加载完成
      if (res.data.data.end_id === res.data.data.last_id) {
        this.finished = true
      }
    },
    async send () {
      if (this.commentVal === '') return this.$toast('输入内容不能为空！')
      const res = await sendCmtAPI({
        target: this.currentItem.com_id,
        content: this.commentVal,
        art_id: this.$route.params.id
      })
      this.list.unshift(...res.data.data.new_obj)
      // eslint-disable-next-line vue/no-mutating-props
      // this.currentItem.reply_count++
      this.commentVal = ''
      this.$toast('回复成功')
    }
  }
}
</script>
<style scoped lang="less">
.van-cell{
  padding: 0;
}
.commentArea{
  display: flex;
  justify-content: space-around;
  margin: 10px;
  padding-bottom: 10px;
  .left{
    width: 45px;
    height: 45px;
    //padding: 10px;
  }
  .middle{
    flex: 1;
    margin-left: 10px;
    .author_name{
      color: #94afff;
      padding-bottom: 6px;
    }
    .comment_val{
      padding-bottom: 6px;
    }
    .timer{
      display: flex;
      align-items: center;
      span{
        color: #ccc;
        margin-right: 10px;
      }
      .van-button{
        padding: 0;
        height: 22px;
        .van-button__text{
          padding: 4px;
          margin: 0;
          color: #000;
        }
      }
    }
  }
}
.commit{
  padding: 10px;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 60px;
  .van-cell{
    position: fixed;
    bottom: 0;
    left: 0;
  }
  .van-field{
    border: none;
  }
  .van-button--primary{
    background:linear-gradient(to right top,#ff6464,#fd6633);
    border: none;
  }
}

</style>
