<template>
  <div>
    <van-nav-bar title="编辑资料" left-arrow @click-left="$router.go(-1)"/>
    <div class="userImage">
      <van-image
        round
        fit="cover"
        :src="userInfo.photo"
      />
      <van-uploader  :after-read="afterRead"/>
    </div>
    <van-cell-group>
      <van-cell title="名称" :value="userInfo.name" @click="changeName" is-link/>
      <van-cell title="性别" :value="userInfo.gender?'女':'男'" @click="genderShow=true" is-link/>
      <van-cell @click="showBirthday" title="生日" :value="userInfo.birthday" is-link/>
    </van-cell-group>

<!--    弹出框-修改名字-->
    <van-dialog v-model="nameShow" title="修改名字" @confirm="updateName" show-cancel-button >
      <van-field v-model.trim="editName" ref="txtName" placeholder="" />
    </van-dialog>

<!--    弹出框-修改性别-->
    <van-popup v-model="genderShow" position="bottom" >
      <van-nav-bar title="修改性别" left-text="取消" @click-left="genderShow=false"/>
      <van-cell title="男"  is-link @click="male"/>
      <van-cell title="女"  is-link @click="fomale"/>
    </van-popup>

<!--    弹出框-修改日期-->
    <van-popup  v-model="birthShow" position="bottom" :style="{ height: '50%' }">
      <van-datetime-picker
        @confirm="updateBirth"
        @cancel="birthShow=false"
        v-model="currentDate"
        type="date"
        title="选择年月日"
        :min-date="minDate"
        :max-date="maxDate"
      />
    </van-popup>

<!--    裁剪区域-->
    <div class="crop-box" v-show="cropShow">
      <!--
       img： 设置要裁剪的图片
       autoCrop： 代表给一个裁剪框
      -->
      <vueCropper
        ref="cropper"
        :img="img"
        autoCrop
        autoCropWidth="200"
        autoCropHeight="200"
      ></vueCropper>

      <!-- 按钮 -->
      <van-button type="primary" @click="doCrop">裁剪</van-button>
      <van-button style="right:0;" type="primary" @click="cropShow=false">取消</van-button>

    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { editInfoAPI, editPhotoAPI } from '@/api'
import moment from 'moment'
import { VueCropper } from 'vue-cropper'
export default {
  name: 'MineEdit',
  data () {
    return {
      nameShow: false,
      editName: '',
      birthShow: false,
      minDate: new Date(1950, 0, 1),
      maxDate: new Date(),
      currentDate: '',
      genderShow: false,
      genderChange: '',
      cropShow: false,
      img: ''
    }
  },
  components: {
    VueCropper
  },
  computed: {
    ...mapState(['userInfo'])
  },
  created () {
    this.$store.dispatch('reqUserInfo')
  },
  methods: {
    changeName () {
      // 显示弹窗
      this.nameShow = true
      // 显示名字
      this.editName = this.userInfo.name
      // this.$nextTick(() => {
      //   this.$refs.txtName.focus()
      // })
      setTimeout(() => {
        this.$refs.txtName.focus()
      }, 500)
    },
    async updateName () {
      if (this.editName === '') return this.$toast('修改名字不能为空')
      // 调用接口发请求修改资料
      await editInfoAPI({
        name: this.editName
      })
      // 此时是修改完了，但是要刷新才能看到效果
      // 为了不刷新，我们就应该把修改后的结果自己改动到vuex里
      this.$store.commit('changeUserInfo', {
        ...this.userInfo,
        name: this.editName
      })
    },
    showBirthday () {
      this.birthShow = true
    },
    async updateBirth () {
      const birthday = moment(this.currentDate).format('YYYY-MM-DD')
      await editInfoAPI({ birthday })
      this.birthShow = false
      this.$store.commit('changeUserInfo', {
        ...this.userInfo,
        birthday
      })
    },
    // ShowGender () {
    //   this.genderShow = true
    // },
    async male () {
      this.genderShow = false
      await editInfoAPI({
        gender: this.genderChange = false
      })
      this.$store.commit('changeUserInfo', {
        ...this.userInfo,
        gender: this.genderChange = false
      })
    },
    async fomale () {
      this.genderShow = false
      await editInfoAPI({
        gender: this.genderChange = true
      })
      this.$store.commit('changeUserInfo', {
        ...this.userInfo,
        gender: this.genderChange = true
      })
    },

    // 裁剪图片
    afterRead (data) {
      // 把选择图片的base64给裁剪绑定的img
      this.img = data.content
      // 把裁剪容器显示出来
      this.cropShow = true
    },
    doCrop () {
      // 要获取到裁剪后的结果
      this.$refs.cropper.getCropBlob(async (data) => {
        // ajax要上传文件，能直接上传吗？不能
        // 要想实现文件的上传，得使用 FormData
        const fm = new FormData()
        fm.append('photo', data)

        const res = await editPhotoAPI(fm)
        // 把图片上传后的地址更新到vuex里
        this.$store.commit('changeUserInfo', {
          ...this.info,
          photo: res.data.data.photo
        })
        // 隐藏裁剪区域
        this.cropShow = false
      })
    }
  }
}
</script>
<style lang="less">
.userImage{
  height: 140px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  .van-image{
  width: 120px;
  height: 120px;
}
    .van-uploader {
      width: 100%;
      height: 100%;
      position: absolute;
      // 仅仅只是改透明度，但是他还在
      opacity: 0;

      .van-uploader__wrapper {
        height: 100%;

        .van-uploader__upload {
          width: 100%;
          height: 100%;
        }
      }
    }
  }

.van-field{
  border: 1px solid #ccc;
}
.van-nav-bar__text{
  font-size: 16px;
  color: #fff!important;
}
.crop-box {

  width: 100vw;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  background-color: #f00;
  .van-button {
    position: absolute;
    bottom: 0;
  }
}
</style>
