import Vue from 'vue'
import Vuex from 'vuex'
import { setToken, getToken, removeToken } from '@/untils/auth'
// import VuexPersistence from 'vuex-persist'
import { userInfoAPI } from '@/api'

Vue.use(Vuex)

// const vuexLocal = new VuexPersistence({
//   storage: window.localStorage
// })

export default new Vuex.Store({
  state: {
    // vuex在每次页面一打开或者一刷新
    // 就能从本地存储获取到之前存的token

    // 左边有值就用左边，没有就用右边
    tokenObj: getToken() || {},
    userInfo: {},
    newComment: '',
    rootComments: [],
    Count: '',
    result: ''
  },
  getters: {
  },
  mutations: {
    saveVal (state, val) {
      state.result = val
    },
    changeTokenObj (state, obj) {
      state.tokenObj = obj
      // 还要保存到本地存储
      setToken(obj)
    },
    changeUserInfo (state, obj) {
      state.userInfo = obj
    },
    logout (state) {
      state.tokenObj = {}
      removeToken()
    },
    setComment (state, val) {
      state.rootComments.push(...val)
    },
    addComment (state, val) {
      state.rootComments.unshift(val)
    },
    addCommentCount (state, val) {
      state.Count = val
    },
    updateComment (state) {
      state.rootComments = []
    }
  },
  actions: {
    async reqUserInfo (ctx) {
      // 如果没数据才发请求
      if (!ctx.state.userInfo.name) {
        // 发请求获取用户信息
        const res = await userInfoAPI()
        // 把数据存到vuex里
        ctx.commit('changeUserInfo', res.data.data)
      }
    }
  },
  modules: {
  }
  // plugins: [vuexLocal.plugin]
})
