<template>
  <div>
    <!--评论区-->
    <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
      <van-cell v-for="(item,idx) in rootComments" :key="idx" >
        <template>
          <div class="commentArea">
            <div class="left">
              <van-image round fit="cover" width="100%" height="100%" :src="item.aut_photo"/>
            </div>
            <div class="middle">
              <div class="author_name">{{ item.aut_name }}</div>
              <div class="comment_val">
                {{item.content}}
              </div>
              <div class="timer">
                <span>{{ item.pubdate | relvTime }}</span>
                <van-button @click="reply(item)">回复({{ item.reply_count }})</van-button>
              </div>
            </div>
            <div class="right">
              <van-icon name="like-o" />
            </div>
          </div>
        </template>
      </van-cell>
    </van-list>

        <!--弹出回复面板-->
    <van-action-sheet v-model="replyShow" title="回复列表" >
        <reply-comment :currentItem="currentItem" v-if="replyShow"></reply-comment>
    </van-action-sheet>

  </div>
</template>
<script>
import replyComment from '@/views/home/replyComment'
import { getCmtAPI } from '@/api'
import { mapState } from 'vuex'
export default {
  name: 'myComment',
  data () {
    return {
      list: [],
      loading: false,
      finished: false,
      offset: null,
      cmt: {},
      replyShow: false,
      currentItem: {}
    }
  },
  components: {
    replyComment
  },
  computed: {
    ...mapState(['rootComments'])
  },
  created () {
    this.$store.commit('updateComment')
  },
  methods: {
    async onLoad () {
      const res = await getCmtAPI({
        type: 'a',
        source: this.$route.params.id,
        offset: this.offset
      })
      this.cmt = res.data.data.results
      // console.log(this.$route.params.id)
      // console.log(res)
      // this.list.push(...this.cmt)
      this.$store.commit('setComment', this.cmt)
      // this.list.unshift(this.$store.state.newComment)
      // 把本次的最后一条id作为下次请求的参数
      this.offset = res.data.data.last_id
      // 加载状态结束
      this.loading = false

      // 数据全部加载完成
      if (res.data.data.end_id === res.data.data.last_id) {
        this.finished = true
      }
    },
    reply (item) {
      this.replyShow = true
      this.currentItem = item
    }
  }
}
</script>
<style scoped lang="less">
.van-cell{
  padding: 0;
}
.commentArea{
  display: flex;
  justify-content: space-around;
  margin: 10px;
  padding-bottom: 10px;
.left{
  width: 45px;
  height: 45px;
//padding: 10px;
}
.middle{
  flex: 1;
  margin-left: 10px;
.author_name{
  color: #94afff;
  padding-bottom: 6px;
}
.comment_val{
  padding-bottom: 6px;
}
.timer{
  display: flex;
  align-items: center;
span{
  color: #ccc;
  margin-right: 10px;
}
.van-button{
  padding: 0;
  height: 22px;
.van-button__text{
  padding: 4px;
  margin: 0;
  color: #000;
}
}
}
}
}
.commit{
  padding: 10px;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 60px;
  .van-cell{
    position: fixed;
    bottom: 0;
    left: 0;
  }
  .van-field{
    border: none;
  }
  .van-button--primary{
    background:linear-gradient(to right top,#ff6464,#fd6633);
    border: none;
  }
}

</style>
