<template>
  <div class="artList"  ref="list"       @scroll="onScroll">
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
    <!--
        load事件绑定的onLoad，专门用来加载数据的方法
        调用时机：
            当数据没铺满时自动调用
            当你往上拉拉到底也会自动调用

        finished用来标记是否全部加载完成，默认为false（否则页面一打开就显示加载完了）
            当他的值如果为true就代表全部加载完成，onLoad就不会再被调用了

        v-model绑定的loading，它代表本次加载数据的状态
            为false代表当前没有在加载数据(或者说本次加载完了，可以进行下次加载)
            为true代表当前还正在加载中，不能进行下次加载

            当onLoad被调用时，会自动把它的值改成true，代表正在加载中
            当加载完数据一定要记得手动把这个值改成false，那样才能进行下次加载
     -->
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <van-cell v-for="item in list" :key="item.art_id" :to="'/articleDetail/'+item.art_id">
        <!--文章标题-->
        <div class="articleTitle">
          {{item.title}}
        </div>
        <!--文章图片-->
        <van-grid :border="false" :column-num="item.cover.type" >
          <van-grid-item v-for="(val,idx) in item.cover.images" :key="idx" v-lazy="val">
            <van-image :src="val" />
          </van-grid-item>
        </van-grid>
        <!--文章信息-->
        <div class="articleInfo">
          <span class="author">{{item.aut_name}}</span>
          <span class="comment">{{item.comm_count}}评论</span>
          <span class="date">{{item.pubdate | relvTime}}</span>
        </div>
      </van-cell>

    </van-list>
    </van-pull-refresh>

  </div>
</template>

<script>
import { articleListAPI } from '@/api'
import { Toast } from 'vant'

export default {
  props: ['channel_id'],
  data () {
    return {
      list: [],
      loading: false,
      finished: false,
      timestamp: Date.now(),
      isLoading: false,
      scrollTop: 0
    }
  },
  activated () {
    this.$refs.list.scrollTop = this.scrollTop
  },

  methods: {
    onScroll () {
      this.scrollTop = this.$refs.list.scrollTop
    },
    async onRefresh () {
      Toast('刷新成功')
      const res = await articleListAPI({
        channel_id: this.channel_id,
        timestamp: this.timestamp
      })
      this.list = res.data.data.results
      this.isLoading = false
    },
    async onLoad () {
      const res = await articleListAPI({
        channel_id: this.channel_id,
        timestamp: this.timestamp
      })
      // 这个list在被重新赋值。所以它的长度永远不会超过10
      // 就导致永远铺不满一个屏幕，就会一直调用onLoad,然后加载完了后又是在替换
      // 所以屏幕就一直在加载和替换数据
      // this.list = res.data.data.results

      // 所以我们应该把得到的数据一一铺开再追加到list
      this.list.push(...res.data.data.results)

      // 把上一段新闻的时间戳赋值给timestamp，方便下次去查找下一段新闻
      this.timestamp = res.data.data.pre_timestamp

      // 加载状态结束
      this.loading = false

      // 数据全部加载完成
      if (this.list.length == null) {
        this.finished = true
      }
    }
  }
}
</script>

<style scoped lang="less">
.artList{
  position: fixed;
  top:86px;
  bottom: 46px;
  overflow: auto;
  width: 100%;
}
.articleInfo{
  span{
    margin: 0 10px;
    color: #ccc;
  }
}

</style>
