<template>
  <div class="container" >
    <van-nav-bar
      fixed
      left-arrow
      @click-left="$router.back()"
      title="小智同学"
    ></van-nav-bar>
    <!-- 聊天面板区域 -->
    <div class="chat-list" ref="chatList">
      <template v-for="(item, index) in list">
        <!-- 左侧是机器人小智 -->
        <div v-if="!item.isMe" class="chat-item left" :key="index">
          <van-image
            fit="cover"
            round
            src="https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fimg.jj20.com%2Fup%2Fallimg%2F1115%2F101021113337%2F211010113337-6-1200.jpg&refer=http%3A%2F%2Fimg.jj20.com&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=auto?sec=1653796439&t=25368161528ce302025d65bbe14c2db4"
          />
          <div class="chat-pao">{{ item.msg }}</div>
        </div>

        <!-- 右侧是当前用户 -->
        <div v-else class="chat-item right" :key="index">
          <div class="chat-pao my">{{ item.msg }}</div>
          <van-image
            fit="cover"
            round
            src="https://img.yzcdn.cn/vant/cat.jpeg"
          />
        </div>
      </template>
    </div>
    <div class="reply-container van-hairline--top">
      <van-field placeholder="说点什么..."  v-model.trim="msg"
                 @keyup.enter="send">
        <span slot="button" style="font-size:12px;color:#999" @click="send">
          提交
        </span>
      </van-field>
    </div>
  </div>
</template>

<script>
import { io } from 'socket.io-client'

export default {
  name: 'UserChat',
  data () {
    return {
      socket: null,
      msg: '',
      list: []
    }
  },
  methods: {
    send () {
      if (!this.msg) return this.$toast('请输入内容')
      this.socket.emit('message', {
        msg: this.msg,
        timestamp: Date.now()
      })
      this.list.push({
        isMe: true,
        msg: this.msg
      })
      this.$nextTick(() => {
        // 滚动到最后
        this.$refs.chatList.scrollTop = this.$refs.chatList.scrollHeight
      })
      this.msg = ''
    }
  },
  created () {
    this.socket = io('http://geek.itheima.net', {
      query: {
        token: this.$store.state.tokenObj.token
      },
      transports: ['websocket']
    })
    // 监听服务端信息
    this.socket.on('message', (data) => {
      this.list.push({
        isMe: false,
        msg: data.msg
      })
      this.$nextTick(() => {
        // 滚动到最后
        this.$refs.chatList.scrollTop = this.$refs.chatList.scrollHeight
      })
    })
  }
}
</script>

<style lang="less" scoped>
.container {
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  box-sizing: border-box;
  background: #fafafa;
  padding: 46px 0 50px 0;
  .chat-list {
    height: 100%;
    overflow-y: scroll;
    .chat-item {
      padding: 10px;
      .van-image {
        vertical-align: top;
        width: 40px;
        height: 40px;
      }
      .chat-pao {
        vertical-align: top;
        display: inline-block;
        max-width: 70%;
        min-height: 40px;
        line-height: 38px;
        border: 0.5px solid #c2d9ea;
        border-radius: 4px;
        position: relative;
        padding: 0 10px;
        background-color: #e0effb;
        word-break: break-all;
        font-size: 14px;
        color: #333;
        &::before {
          content: '';
          width: 10px;
          height: 10px;
          position: absolute;
          top: 12px;
          border-top: 0.5px solid #c2d9ea;
          border-right: 0.5px solid #c2d9ea;
          background: #e0effb;
        }
      }
      .chat-pao.my {
        background-color: #9eea6a;
        &::before {
          content: '';
          background: #9eea6a;
        }
      }
    }
  }
}
.chat-item.right {
  text-align: right;
  .chat-pao {
    margin-left: 0;
    margin-right: 15px;
    &::before {
      right: -6px;
      transform: rotate(45deg);
    }
  }
}
.chat-item.left {
  text-align: left;
  .chat-pao {
    margin-left: 15px;
    margin-right: 0;
    &::before {
      left: -5px;
      transform: rotate(-135deg);
    }
  }
}
.reply-container {
  position: fixed;
  left: 0;
  bottom: 0;
  height: 44px;
  width: 100%;
  background: #f5f5f5;
  z-index: 9999;
}
</style>
