<template>
  <div>
    <van-nav-bar :title="$store.state.result+' 的搜索结果'" left-arrow @click-left="$router.go(-1)" />

    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <van-cell :value="item.title" v-for="item in list" :key="item.art_id" :to="'/articleDetail/'+item.art_id"/>
    </van-list>
  </div>
</template>
<script>
import { searchResultAPI } from '@/api'

export default {
  name: 'searchResult',
  data () {
    return {
      list: [],
      loading: false,
      finished: false,
      page: 1
    }
  },
  methods: {
    async onLoad () {
      const res = await searchResultAPI({
        q: this.$store.state.result,
        page: this.page
      })
      this.list.push(...res.data.data.results)
      this.page++
      // 加载状态结束
      this.loading = false

      // 数据全部加载完成
      if (this.list.length >= res.data.data.total_count) {
        this.finished = true
      }
    }
  }
}
</script>
<style scoped></style>
