<template>
  <div id="article">
    <van-nav-bar title="文章详情" left-arrow @click-left="$router.go(-1)" fixed placeholder ></van-nav-bar>
    <div class="article">
      <!--文章标题-->
      <div class="article_title"><h1>{{ detail.title }}</h1></div>
      <!--相关信息-->
      <div class="article_info">
        <div class="left">
          <van-image
            round
            fit="cover"
            width="100%"
            height="100%"
            :src="detail.aut_photo"
          />
        </div>
        <div class="middle">
          <h1>{{detail.aut_name}}</h1>
          <span>{{ detail.pubdate| relvTime }}</span>
        </div>
        <div class="right">
          <van-button icon="plus" type="primary" round>关注</van-button>
        </div>
      </div>
      <!--正文-->
      <div class="article_detail" v-html="detail.content">
<!--        {{detail.data.data.content}}-->
      </div>
      <!--end-->
      <div class="end">
        <van-divider>end</van-divider>
        <div class="tool_chose">
          <van-button id="like" icon="good-job-o" round>点赞</van-button>
          <van-button id="dislike" icon="delete-o" round>不喜欢</van-button>
        </div>
      </div>

      <comment></comment>
    </div>
    <!--底部评论区-->
    <div class="commit">
      <van-cell >
        <van-field
          center
          clearable
          placeholder="请输入评论内容"
          v-model="commentVal"
        >
          <template #button>
            <van-button size="small" type="primary" @click="sendComment" >发布</van-button>
          </template>
        </van-field>
      </van-cell>
    </div>
  </div>
</template>
<script>
import { articleDetailListAPI, sendCmtAPI } from '@/api'
import comment from '@/views/home/comment'
export default {
  name: 'articleDetail',
  components: { comment },
  data () {
    return {
      commentVal: '',
      detail: {},
      list: []
    }
  },
  async created () {
    const res = await articleDetailListAPI(this.$route.params.id)
    this.detail = res.data.data
  },
  methods: {
    async sendComment () {
      if (this.commentVal === '') {
        this.$toast('输入内容不能为空！')
        return
      }
      const res = await sendCmtAPI({
        target: this.$route.params.id,
        content: this.commentVal
      })
      // console.log(res)
      this.$store.commit('addComment', res.data.data.new_obj)
      this.commentVal = ''
    }
  }
}
</script>
<style scoped lang="less">
#article{
  position: relative;
  .article{
    padding: 5px;

    .article_title{

      h1{
        font-size: 20px;
        padding: 2px;
      }
    }
    .article_info{
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 14px 0;
      .left{
        width: 60px;
        height: 60px;
        padding: 14px;
      }
      .middle{
        //display: block;
        flex: 1;
      }
      .right{
        .van-button--primary{
          border: none;
          background: linear-gradient(to right top,#ff6464,#fd6633);
          height: 32px;
          width: 70px;
          padding: 10px;
          margin-right: 10px;
        }
      }
    }
    .article_detail{
      padding: 5px;
      word-wrap: break-word;
      ::v-deep {
        img{
          width: 100%;
        }
        pre{
          overflow: auto;
          background-color: #000;
          color: #dcdcdc;
        }
      }
      }
    .end{
      .tool_chose{
        display:flex;
        justify-content:space-around;
        ::v-deep #like{
          border: 1px solid #42b983;
          color: #42b983;
          width: 110px;
          height: 40px;
        }
        ::v-deep #dislike{
          border: 1px solid #ff0000;
          color: #ff0000;
          width: 110px;
          height: 40px;
        }
      }
    }
  }
  .commit{
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 60px;
    .van-cell{
      position: fixed;
      bottom: 0;
      left: 0;
    }
    .van-field{
  border: none;
    }
    .van-button--primary{
      background:linear-gradient(to right top,#ff6464,#fd6633);
      border: none;
    }
  }
}

</style>
