import request from '@/untils/request'

export const articleList = (params) => {
  return request({
    url: '/v1_0/articles',
    params
  })
}
export const articleDetailList = (id) => {
  return request({
    url: '/v1_0/articles/' + id
  })
}
export const getCmt = (params) => {
  return request({
    url: '/v1_0/comments',
    params
  })
}
export const sendCmt = (data) => {
  return request({
    url: '/v1_0/comments',
    method: 'post',
    data
  })
}
